@import url(https://fonts.googleapis.com/css2?family=Open+Sans);
header {
  background-color: #282c34;
  margin: 0;
  padding: 7px 20px;
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 10px 260px auto 135px;
  grid-column-gap: 7px;
  -webkit-column-gap: 7px;
          column-gap: 7px;
  grid-template-areas: "icon head . opt";
  box-shadow: 0 0 100px #ff4500;
}
.header_headingDiv {
  grid-area: head;
}

.header_optDiv {
  grid-area: opt;
  padding: 3px 6px;
  border-radius: 7px;
  background-color: #515969;
}

.header_headingDiv h1 {
  margin: 0;
  padding: 7px 7px;
  font-size: 32px;
  font-family: "Open Sans", sans-serif;
  color: #ff4500;
}
@media screen and (max-width: 500px) {
  .header_headingDiv h1 {
    padding: 4px;
  }
}

footer {
  background-color: #282c34;
  padding: 0px 20px;
  display: grid;
  grid-template-columns: minmax(150px,350px) auto;
  grid-column-gap: 50px;
  -webkit-column-gap: 50px;
          column-gap: 50px;
  grid-template-areas: "what link";
}

.footer_whatDiv {
  grid-area: what;
  padding: 5px;
}

.footer_linkDiv {
  grid-area: link;
  padding: 5px;
}

.linksList {
  list-style: none;
}

main {
  margin: 2px 20px;
  background-color: #515969;
  height: 73.5vh;
  box-shadow: 0 0 30px #282c34;
}

h2 {
  text-align: center;
  font-size: 45px;
  padding: 0;
  margin: 50px 0;
}

html,
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, "Segoe UI", "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  min-width: 340px;
}

#root {
  min-width: 360px;
  box-sizing: border-box;
  height: 100%;
}

